import React, { useState, useEffect } from 'react';
import axios from 'axios';

function StudentsEdit() {



    const [student, setStudent] = useState({})

    useEffect(() => {
        axios({
            method: "POST",
            url: 'http://127.0.0.1:8000/api/students/',
            data: student
        }).then(() => {
        })
    }, [])

    return (
        <form onSubmit={e => setStudent({
            surname: e.target.surname.value,
            name: e.target.name.value,
            patronymic: e.target.patronymic.value,
            course: e.target.course.value,
            group: e.target.group.value
        })} >
            <input type="text" name="surname" placeholder="Фамилия" />
            <input type="text" name="name" placeholder="Фамилия" />
            <input type="text" name="patronymic" placeholder="Фамилия" />
            <input type="number" name="course" placeholder="Фамилия" />
            <input type="text" name="group" placeholder="Фамилия" />
            <input type="submit" value="Отправить" />
        </form>
    );
}

export default StudentsEdit;